.team-section {
  padding: 2rem 1rem;
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}

.team-heading {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 2rem;
  color: var(--color-secondary);
}

.team-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 2rem;
}

.team-card {
  background-color: white;
  border-radius: 1rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  padding: 1.5rem;
  text-align: center;
  transition: transform 0.3s ease;
}

.team-card:hover {
  transform: translateY(-5px);
}

.team-avatar {
  width: 100px;
  height: 100px;
  border-radius: 9999px; /* fully round */
  object-fit: cover;
  margin: 0 auto 1rem auto;
  border: 2px solid #ddd;
}

.team-name {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 0.25rem;
  color: var(--color-secondary);
}

.team-role {
  font-size: 0.9rem;
  color: #666;
  white-space: pre-line; /* handles \n in role text */
  margin-bottom: 0.75rem;
}

.team-description {
  font-size: 0.9rem;
  color: #444;
}
