.faq-container {
  max-width: 960px;
  margin: 4rem auto;
  padding: 2rem;
  background-color: #ffffff;
  border-radius: 12px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.05);
}

.faq-title {
  font-size: 2.5rem;
  text-align: center;
  color: #00bfa6; /* Teal accent */
  margin-bottom: 2rem;
}

.faq-list {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.faq-item {
  border-left: 4px solid #00bfa6;
  padding-left: 1rem;
  background: #f9f9f9;
  border-radius: 8px;
  padding: 1rem;
  transition: all 0.3s ease;
}

.faq-item:hover {
  background-color: #f1fefc;
  transform: translateX(5px);
}

.faq-question {
  font-size: 1.25rem;
  color: #222;
  margin-bottom: 0.5rem;
}

.faq-answer {
  font-size: 1rem;
  color: #555;
  line-height: 1.6;
}
